import React, { useState, useEffect } from "react";
// import  useState ,{useEffect} from 'react'
import { DataGrid } from "@mui/x-data-grid";
import axios from "axios";
import Box from "@mui/material/Box";

const columns = [
  {
    field: "id",
    headerName: "IDMeal",
    width: 90,
    headerClassName: "super-app-theme--header",
  },
  {
    field: "strMeal",
    headerName: "Str Meal",
    editable: true,
    headerClassName: "super-app-theme--header",
    width: 200,
  },
  {
    field: "strCategory",
    headerName: "Str Category",
    editable: true,
    headerClassName: "super-app-theme--header",
    width: 200,
  },
  {
    field: "strArea",
    headerName: "Str Area",
    editable: true,
    headerClassName: "super-app-theme--header",
    width: 200,
  },
  {
    field: "strInstructions",
    headerName: "Str Instructions",
    description: "This column has a value large and is not sortable.",
    sortable: false,
    headerClassName: "super-app-theme--header",
    width: 750,
  },
  {
    field: "strTags",
    headerName: "Str Tags",
    editable: true,
    headerClassName: "super-app-theme--header",
    width: 200,
  },
  {
    field: "strYoutube",
    headerName: "Str Youtube",
    editable: true,
    headerClassName: "super-app-theme--header",
    width: 200,
  },
];

export default function DataGridDemo() {
  const [rows, setRows] = useState([]);
  const [loading, setLoading] = useState(true);
  const rows1 = [];

  useEffect(() => {
    fetchData();

    // console.log("d"+JSON.stringify(d))
  }, []);
  async function fetchData() {
    try {
      for (let i = 0; i <= 7; i++) {
        let object = {};
        let url = `https://www.themealdb.com/api/json/v1/1/lookup.php?i=5277${i}`;
        await axios.get(url).then((response) => {
          object.id = i;
          object.strMeal = response.data.meals[0].strMeal;
          object.strCategory = response.data.meals[0].strCategory;
          object.strArea = response.data.meals[0].strArea;
          object.strInstructions = response.data.meals[0].strInstructions;
          object.strTags = response.data.meals[0].strTags;
          object.strYoutube = response.data.meals[0].strYoutube;
          rows1.push(object);

          if (i === 7) {
            datachecking(rows1);
          }
        });
      }
    } catch (e) {
      console.log("error" + JSON.stringify(e));
    }
  }
  function datachecking(rowdata) {
    var result = rowdata.reduce((unique, o) => {
      if (!unique.some((obj) => obj.id == o.id)) {
        unique.push(o);
      }
      return unique;
    }, []);
    console.log("rowdata" + JSON.stringify(result));
    setRows(result);
    setLoading(false);
  }

  return (
    <div style={{ height: 868, width: "100%", paddingTop: "25px" }}>
      {loading == true ? (
        <div style={{ color: "black" }}>Loading...</div>
      ) : (
        <Box
          sx={{
            height: 300,
            width: 1,
            "& .super-app-theme--header": {
              backgroundColor: "rgba(255, 7, 0, 0.55)",
            },
          }}
        >
          {" "}
          <DataGrid
            rows={rows}
            columns={columns}
            pageSize={5}
            rowsPerPageOptions={[5]}
            checkboxSelection
            disableSelectionOnClick
          />
        </Box>
      )}
    </div>
  );
}
